export default {
  install(app, args = {}) {
    app.config.globalProperties.$alert = (options) => {
      var div = document.createElement("div");
      div.setAttribute("id", "alert-container");
      const alert = ` <div
        class="modal fade show"
        id="modal-default"
        tabindex="-1"
        role="dialog"
        style="display: block"
      >
        <div
          class="modal-dialog modal- modal-dialog-centered modal-sm"
          role="document"
        >
          <div class="modal-content" style="border-color: transparent">
            <div class="modal-header py-2">
              <h5 class="modal-title">
                ${options.title}
              </h5>
              <a
                href="javascript:void(0)"
                class="close text-dark"
              >
                <em class="icon demo-pli-cross"></em
              ></a>
            </div>
  
            <div class="modal-body text-left h6 mb-0">
                ${options.content}
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="mr-auto ${options.classes}"
                id="action-btn"
              >
                <span id="action-text">${options.actionText}</span>
                <p id="alert-loading"><span></span><span></span><span></span></p>
              </button>
              <button
                type="button"
                class="btn btn-light ms-auto"
                id="close-btn"
              >
              <span>${options.closeText}</span>
              </button>
            </div>
          </div>
        </div>
      </div>
      <div class="modal-backdrop fade show"></div>`;
      div.innerHTML = alert;
      div.querySelector(".close").addEventListener("click", () => {
        document.querySelector("#alert-container").remove();
        args.store.state.hasActiveModal = false;
      });
      div.querySelector("#close-btn").addEventListener("click", () => {
        document.querySelector("#alert-container").remove();
        args.store.state.hasActiveModal = false;
      });
      div.querySelector("#action-btn").addEventListener("click", () => {
        args.store.state.hasActiveModal = true;
        args.store.state.requestFlag = "HANDLING_ALERT_ACTION";
        div.querySelector("#action-text").remove();
        div.querySelector("#alert-loading").classList.add("d-block");
        options.action();
      });
      document.body.prepend(div);
    };
  },
};
