import { createStore } from "vuex";
import { encodeQuery } from "../common/utils.js";
import axios from "axios";
axios.withCredentials = true;
const baseURL =
  process.env.NODE_ENV === "production"
    ? "/api/pos/"
    : "http://127.0.0.1:8000/api/pos/";
axios.defaults.baseURL = baseURL;

export default createStore({
  state: {
    status: "",
    timeZone: localStorage.getItem("_tz") || "Africa/Kigali",
    today:
      localStorage.getItem("today") ||
      new Date()
        .toLocaleString("fr-CA", { timeZone: "Africa/Kigali" })
        .slice(0, 10),
    token: localStorage.getItem("token") || "",
    branch: JSON.parse(localStorage.getItem("_branch")) || {},
    user: {},
    isLoading: false,
    requestFlag: null,
    isCorrectSystemDay: false,
    drawerOpen: true,
    entityImagePost: {
      record_id: null,
      url: null,
    },
    deleteAlert: {
      title: null,
      content: null,
      actionType: null,
      actionButton: null,
      classes: null,
      modalOpen: false,
    },
    recordDeleter: {
      entityName: null,
      url: null,
      media: {
        recordImage: null,
        galleryTable: null,
        mediaPath: null,
        galleryParentRef: null,
      },
    },
    currentContent: {},
    responseData: {},
    hasActiveModal: false,
    flashMessage: {},
    branches: [],
    settings: {
      site_address: null,
      site_name: null,
      currency: null,
      contact_one: null,
      app_phone: null,
      app_email: null,
      app_tin: null,
    },
    notifications: {
      requisitions: 0,
      purchase_orders: 0,
      suppliers_payments: 0,
      pending_orders: 0,
    },
  },

  mutations: {
    SET_APP_SETTINGS(state, settings) {
      state.settings = settings;
      localStorage.setItem("_tz", settings.timezone);
      localStorage.setItem("today", settings.today);
    },
    SET_LOCKED_BLANCH(state, branch) {
      if (branch != null)
        localStorage.setItem("_branch", JSON.stringify(branch));
      else localStorage.removeItem("_branch");
      state.branch = branch;
    },
    SET_REQUEST_FLAG(state, flag) {
      state.requestFlag = flag;
    },
    SET_FLASH_MESSAGE(state, message) {
      state.flashMessage = message;
      setTimeout(() => {
        state.flashMessage = {};
      }, 5000);
    },
    AUTH_REQUEST(state) {
      state.status = "loading";
    },
    AUTH_SUCCESS(state, playload) {
      state.status = "success";
      state.token = playload.token;
      state.user = playload.user;
      state.requestFlag = null;
    },
    REQUEST_SUCCESS(state) {
      state.requestFlag = null;
      if (state.hasActiveModal) {
        state.hasActiveModal = false;
        document.querySelector("#alert-container").remove();
      }
    },
    REQUEST_ERROR(state) {
      state.status = "error";
      state.requestFlag = null;
      if (state.hasActiveModal) {
        state.hasActiveModal = false;
        document.querySelector("#alert-container").remove();
      }
    },
    logout(state) {
      state.status = "";
      state.token = "";
    },
    SET_POST_DATA(state, args) {
      state.entityImagePost = {
        record_id: args.record_id,
        url: args.url,
      };
    },
    SET_CURRENT_USER(state, user) {
      state.user = user;
    },
    SET_DB_RESPONSE(state, responseData) {
      if (responseData) {
        state.responseData = { ...state.responseData, ...responseData };
      }
    },
    SET_BRANCHES(state, branches) {
      state.branches = branches;
    },
    SET_MENU_NOTIFICATIONS(state, records) {
      state.notifications = { ...state.notifications, ...records };
    },
    REDUCE_NOTIFICATIONS(state, obj) {
      state.notifications[obj.key] -= obj.total || 1;
    },
  },
  actions: {
    SetEntityPostData({ commit }, args) {
      return new Promise((resolve) => {
        commit("SET_POST_DATA", args);
        resolve();
      });
    },
    authRequest({ commit }, args) {
      return new Promise((resolve, reject) => {
        commit("AUTH_REQUEST");
        axios({
          url: args.url,
          data: args.formData,
          method: "POST",
        })
          .then((resp) => {
            const token = resp.data.access_token || resp.data.token;
            if (token) {
              localStorage.setItem("token", token);
              axios.defaults.headers.common["Authorization"] = token;
              commit("AUTH_SUCCESS", { token: token, user: resp.data.user });
            }
            resolve(resp);
          })
          .catch((err) => {
            commit("REQUEST_ERROR");
            localStorage.removeItem("token");
            reject(err);
          });
      });
    },
    logout({ commit }) {
      return new Promise((resolve) => {
        commit("logout");
        localStorage.removeItem("_branch");
        localStorage.removeItem("token");
        delete axios.defaults.headers.common["Authorization"];
        localStorage.clear();
        resolve();
      });
    },
    postRequest({ commit }, args) {
      return new Promise((resolve, reject) => {
        const branch = localStorage.getItem("_branch");
        let url = args.url;
        if (branch) {
          url = encodeQuery(url, { current_branch: JSON.parse(branch).id });
        }
        axios
          .post(url, args.formData)
          .then((resp) => {
            commit("REQUEST_SUCCESS");
            commit("SET_DB_RESPONSE", resp.data);
            resolve(resp);
          })
          .catch((err) => {
            if (err.response.status == 401) {
              this.dispatch("logout").then(() => {
                window.location.replace("/");
              });
            }
            if (err.response.status == 500) {
              commit("SET_FLASH_MESSAGE", {
                type: "danger",
                message: "Error Occured. Please contact system administrator",
              });
            }
            commit("REQUEST_ERROR");
            reject(err);
          });
      });
    },
    getRequest({ commit }, url) {
      return new Promise((resolve, reject) => {
        const branch = localStorage.getItem("_branch");
        if (branch) {
          url = encodeQuery(url, { current_branch: JSON.parse(branch).id });
        }
        axios
          .get(url)
          .then((resp) => {
            commit("REQUEST_SUCCESS");
            commit("SET_DB_RESPONSE", resp.data);
            resolve(resp);
          })
          .catch((err) => {
            if (err.response.status == 401) {
              this.dispatch("logout").then(() => {
                window.location.replace("/");
              });
            }
            if (err.response.status == 500) {
              commit("SET_FLASH_MESSAGE", {
                type: "danger",
                message: "Error Occured. Please contact system administrator",
              });
            }
            commit("REQUEST_ERROR");
            reject(err);
          });
      });
    },
  },
  getters: {
    isLoggedIn: (state) => !!state.token,
    authStatus: (state) => state.status,
    user: (state) => state.user,
    branch: (state) => state.branch,
    requestFlag: (state) => state.requestFlag,
  },
});
